import mediaExhibit from './exhibit.jpg'
import mediaBelief from './belief.jpg'
import mediaCreativity from './creativity.jpg'
import mediaEmpathy from './empathy.jpg'
import mediaExpertise from './expertise.jpg'
import mediaPurpose from './purpose.jpg'

import mediaFlyers from './flyers.jpg'
import mediaWorkshops from './workshops2.jpg'
import mediaBuilding from './building.jpg'
import mediaBuilding2 from './building2.jpg'
import mediaPyramid from './pyramid.png'

const downloadsHyperHuman = process.env.PUBLIC_URL + '/downloads/HyperHuman_by_IDEO_2018.pdf'

export default {
  title: "HyperHuman (2018)",
  route: "hyper-human",
  // caption: "What if intelligent machines could benefit our lives, our work, and our society?",
  caption: "Speculating how intelligent machines might benefit our lives, our work, and our society",
  details: "Design Research, Software Design, Exhibition Design @ IDEO",
  content: [
    {
      type: "media",
      content: mediaExhibit
    },
    {
      type: "text",
      content: "With growing fears of AI taking over the world, we imagined an AI revolution where clever machines start accelerating us to a more meaningful society: freeing up our potential so we can focus on whats important, guiding us so we can tackle what matters to us, and expanding our horizons so we can achieve what was once impossible. But what might that feel like?",
    },
    {
      type: "text",
      content: "<a href=\"https://designawards.core77.com/speculative-design/86821/HyperHuman\" target=\"_blank\">Winner, Core77 Design Award 2019 (Speculative Design)</a>",
    },
    // {
    //   type: "text",
    //   content: "<a href=\"https://designawards.core77.com/speculative-design/86821/HyperHuman\" target=\"_blank\">Runner-up, Core77 Design Award 2019 (Interaction Design)</a>",
    // },
    {
      type: "media",
      content: [mediaBelief,mediaCreativity,mediaEmpathy,mediaExpertise,mediaPurpose],
      caption: "AI products of tomorrow?"
    },
    {
      type: "text",
      content: "<a href=\"" + downloadsHyperHuman + "\" target=\"_blank\">View exhibition booklet (.pdf)</a>",
    },
    {
      type: "media",
      content: mediaFlyers,
    },
    {
      type: "text",
      content: "## A global initiative\n\nAs IDEO set out to establish Data Science as a discipline of design, we leveraged the opportunity to understand what those collaborations at the cross-sections of disciplines might look like. It was also a chance to provoke with a point-of-view and engage in a conversation with clients and partners around their own challenges.\n\nWe connected across studios and held generative workshops with the wider IDEO community to gather breadth in our thinking. As a result, we set up a set of project briefs, design tools and assembled diverse teams to tackle them.",
    },
    {
      type: "media",
      content: mediaWorkshops,
    },
    {
      type: "text",
      content: "## Building to learn\n\nWe rooted the challenges in a human need rather than a technological trait (e.g. \"How might we embed our vision, values and beliefs into AI systems to confidently delegate tasks?\"). Within that context teams experimented with machine learning & other AI techniques hands-on, leveraging their technical capabilities and curiosity.\n\nThis approached helped us focus on creating output meaningful to people while constantly demistifying what AI is capable of, surfacing new exciting opportunities to design with and avoiding the pitfalls of technological hype.",
    },
    {
      type: "media",
      content: [mediaBuilding]
    },
    {
      type: "text",
      content: "> As the Design Lead, I set up the project vision, coordinated with design directors and led a global collaboration of multidisciplinary teams (20+ interaction, product, business designers, design researchers and data scientists) across 3 studios to explore and produce a speculative exhibition.\n\n",
    }
  ]
}



